.main_about {
  padding: 80px 0 100px 0;
  &__title {
    text-align: center;
    margin-bottom: 20px;
    color: var(--black-color);
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-weight: 400;
  }
  &__descr {
    margin: 40px auto 0 auto;
    width: 590px;
    font-weight: 400;
    text-align: center;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }
  &__subdescr {
    margin: 20px auto 0 auto;
    width: 590px;
    font-weight: 400;
    text-align: center;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }
}