.main_header {
  min-height: 640px;
  background: url('../../../img/main-img/bg/header-bg.jpg') no-repeat center/cover;
  padding: 180px 0 200px 0;
  font-weight: 700;
  &__title {
    font-weight: 700;
    color: var(--white-color);
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    text-align: center;
    margin: 0 0 20px 0;
  }
  &__subtitle {
    margin: 35px 0 0 0;
    text-align: center;
    color: var(--white-color);
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }
  &__descr {
    margin: 20px 0 0 0;
    text-align: center;
    color: var(--white-color);
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }
  &__btn {
    display: block;
    margin: 20px auto 0 auto;
    padding: 7px 42px;
    color: var(--white-color);
    background-color: transparent;
    border: 1px solid #FFFFFF;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    border-radius: 3px;
  }
}